:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: 250ms;
}

.gallery {
  max-width: 1140px;
  grid-gap: 12px;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  grid-auto-rows: 240px;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  padding: 0;
  list-style: none;
  display: grid;
}

.gallery__item {
  position: relative;
}

.gallery__image:hover {
  transform: scale(1.03);
}

.gallery__image {
  height: 100%;
  width: 100%;
  object-fit: cover;
  transition: transform var(--animation-duration) var(--timing-function);
}

.gallery__link {
  height: 100%;
  text-decoration: none;
  display: block;
}

.gallery__link:hover {
  cursor: zoom-in;
}

body {
  background-color: #121212;
  flex-direction: column;
  align-items: center;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
  display: flex;
}

.cat-list {
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 75px;
  list-style: none;
  display: flex;
}

.cat-card {
  width: 300px;
  background-color: #fff;
  border-radius: 10px;
  margin: 20px;
  transition: transform .3s ease-in-out, box-shadow .3s ease-in-out;
  position: relative;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, .2);
}

.cat-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, .3);
}

.cat-card img {
  width: 100%;
  height: auto;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.cat-info {
  padding: 15px;
}

.cat-info h2 {
  color: #333;
  margin: 0;
  font-size: 1.8em;
}

.cat-info p {
  color: #333;
  margin: 5px 0;
  font-size: 1.4em;
}

.hidden {
  visibility: hidden;
}

