.cat-card {
  position: relative;
  width: 300px;
  margin: 20px;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  background-color: #fff;
  transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
}

.cat-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.3);
}

.cat-card img {
  width: 100%;
  height: auto;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.cat-info {
  padding: 15px;
}

.cat-info h2 {
  margin: 0;
  font-size: 1.8em;
  color: #333;
}

.cat-info p {
  margin: 5px 0;
  font-size: 1.4em;
  color: #333;
}
.hidden {
  visibility: hidden;
}
